@import "https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,400;0,700;1,400&display=swap";
body {
  background: #80deea;
  margin: 100px;
  font-family: Montserrat Alternates, sans-serif;
}

h1 {
  text-align: center;
  font-size: 3rem;
}

.Navbar li {
  margin-right: 5rem;
  display: inline;
}

.Navbar {
  justify-content: space-around;
  align-items: flex-end;
  display: flex;
}

.header {
  align-items: space-around;
  flex-basis: 90%;
  margin-left: 20rem;
  display: flex;
}

.header h1 {
  align-self: flex-end;
}

.links {
  flex-direction: row;
  align-items: flex-end;
  margin: 0 200px 40px;
  display: flex;
}

.links ul {
  align-items: center;
  margin: 0;
  padding: 0;
  display: inline-flex;
}

.links li {
  font-weight: bold;
}

.links img {
  width: 70px;
}

img {
  width: 200px;
}

.Products-items {
  grid-gap: 20px;
  grid-template-columns: repeat(3, 1fr);
  display: grid;
}

.Products-title {
  text-align: center;
  grid-column: 1 / -1;
  font-size: 2rem;
}

.Products {
  box-shadow: 20px;
  background-color: #fff;
  margin: 0 20rem 10rem;
  padding: 3rem;
}

.CartItem {
  justify-content: space-around;
  align-items: center;
  margin: 20px 35rem 0;
  display: flex;
}

.CartItemName {
  flex-basis: 20%;
  margin-top: 0;
  font-weight: bold;
  display: inline-block;
}

.CartItemPrice {
  flex-basis: 20%;
  margin-left: 20px;
  display: inline-block;
}

.CartItem button {
  color: #000;
  width: 30px;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 10px;
  margin: 10px;
  padding: 5px;
}

.checkout button {
  color: #000;
  width: 130px;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 10px;
  margin: 10px;
  padding: 5px;
}

.Cart {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.App {
  text-align: center;
}

